.list {
    margin: 20px;
}

.item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    background-color: white;
    width: 300px;
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
}

.status {
    width: 15px;
    height: 15px;
    margin-right: 15px;
    border-radius: 50%;
}

.online {
    background-color: green;
}

.offline {
    background-color: red;
}

.avatar {
    margin-left: 10px;
    width: 50px;
    height: 50px;
}

.name{
    margin-left: 20px;
    text-align: center;
    font-weight: 700;
    font-size: 22px;
    color: #333;
}